.aplayer-pic {
  position: relative;
  float: left;
  height: 66px;
  width: 66px;
  background-image: url(data:image/jpeg;base64,);
  background-size: cover;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.aplayer-pic .aplayer-button {
    position: absolute;
    border-radius: 50%;
    opacity: 0.8;
    cursor: pointer;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
    -webkit-transition: all 0.1s ease;
    transition: all 0.1s ease;
}
.aplayer-pic .aplayer-button:hover {
      opacity: 1;
}
.aplayer-pic .aplayer-button .aplayer-fill {
      fill: #fff;
}
.aplayer-pic .aplayer-hide {
    display: none;
}
.aplayer-pic .aplayer-play {
    width: 26px;
    height: 26px;
    border: 2px solid #fff;
    bottom: 50%;
    right: 50%;
    margin: 0 -15px -15px 0;
}
.aplayer-pic .aplayer-play .aplayer-icon-play {
      position: absolute;
      top: 3px;
      left: 4px;
      height: 20px;
      width: 20px;
}
.aplayer-pic .aplayer-pause {
    width: 16px;
    height: 16px;
    border: 2px solid #fff;
    bottom: 4px;
    right: 4px;
}
.aplayer-pic .aplayer-pause .aplayer-icon-pause {
      position: absolute;
      top: 2px;
      left: 2px;
      height: 12px;
      width: 12px;
}

.aplayer-icon {
  width: 15px;
  height: 15px;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  opacity: .8;
  vertical-align: middle;
  padding: 0;
  font-size: 12px;
  margin: 0;
  display: inline;
}
.aplayer-icon .aplayer-fill {
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.aplayer-list {
  overflow: auto;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  will-change: height;
  display: none;
}
.aplayer-list.aplayer-list-hide {
    height: 0 !important;
}
.aplayer-list::-webkit-scrollbar {
    width: 5px;
}
.aplayer-list::-webkit-scrollbar-track {
    background-color: #f9f9f9;
}
.aplayer-list::-webkit-scrollbar-thumb {
    border-radius: 3px;
    background-color: #eee;
}
.aplayer-list::-webkit-scrollbar-thumb:hover {
    background-color: #ccc;
}
.aplayer-list ol {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.aplayer-list ol li {
      position: relative;
      height: 32px;
      line-height: 32px;
      padding: 0 15px;
      font-size: 12px;
      border-top: 1px solid #e9e9e9;
      cursor: pointer;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
      overflow: hidden;
}
.aplayer-list ol li:first-child {
        border-top: none;
}
.aplayer-list ol li:hover {
        background: #efefef;
}
.aplayer-list ol li.aplayer-list-light {
        background: #e9e9e9;
}
.aplayer-list ol li.aplayer-list-light .aplayer-list-cur {
          display: inline-block;
}
.aplayer-list ol li .aplayer-list-cur {
        display: none;
        width: 3px;
        height: 22px;
        position: absolute;
        left: 0;
        top: 5px;
        cursor: pointer;
}
.aplayer-list ol li .aplayer-list-index {
        color: #666;
        margin-right: 12px;
        cursor: pointer;
}
.aplayer-list ol li .aplayer-list-author {
        color: #666;
        float: right;
        cursor: pointer;
}

.aplayer-controller {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.aplayer-controller .aplayer-bar-wrap {
    margin: 0 0 0 5px;
    padding: 4px 0;
    cursor: pointer !important;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.aplayer-controller .aplayer-bar-wrap .aplayer-bar {
      position: relative;
      height: 2px;
      width: 100%;
      background: #cdcdcd;
}
.aplayer-controller .aplayer-bar-wrap .aplayer-bar .aplayer-loaded {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        background: #aaa;
        height: 2px;
        -webkit-transition: all 0.5s ease;
        transition: all 0.5s ease;
}
.aplayer-controller .aplayer-bar-wrap .aplayer-bar .aplayer-played {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        height: 2px;
}
.aplayer-controller .aplayer-bar-wrap .aplayer-bar .aplayer-played .aplayer-thumb {
          position: absolute;
          top: 0;
          right: 5px;
          margin-top: -4px;
          margin-right: -10px;
          height: 8px;
          width: 8px;
          border-radius: 50%;
          background: #fff;
          cursor: pointer !important;
}
.aplayer-controller .aplayer-time {
    position: relative;
    right: 0;
    bottom: 3px;
    height: 17px;
    color: #999;
    font-size: 11px;
    padding-left: 7px;
}
.aplayer-controller .aplayer-time .aplayer-time-inner {
      vertical-align: middle;
}
.aplayer-controller .aplayer-time .aplayer-icon {
      cursor: pointer;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
}
.aplayer-controller .aplayer-time .aplayer-icon .aplayer-fill {
        fill: #666;
}
.aplayer-controller .aplayer-time .aplayer-icon.aplayer-icon-mode {
        margin-right: 4px;
}
.aplayer-controller .aplayer-time .aplayer-icon:hover .aplayer-fill {
        fill: #000;
}
.aplayer-controller .aplayer-time .aplayer-icon.aplayer-icon-menu {
        display: none;
}
.aplayer-controller .aplayer-time.aplayer-time-narrow .aplayer-icon-mode {
      display: none;
}
.aplayer-controller .aplayer-time.aplayer-time-narrow .aplayer-icon-menu {
      display: none;
}
.aplayer-controller .aplayer-volume-wrap {
    position: relative;
    display: inline-block;
    margin-left: 3px;
    cursor: pointer !important;
}
.aplayer-controller .aplayer-volume-wrap:hover .aplayer-volume-bar-wrap {
      display: block;
}
.aplayer-controller .aplayer-volume-wrap .aplayer-volume-bar-wrap {
      display: none;
      position: absolute;
      bottom: 15px;
      right: -3px;
      width: 25px;
      height: 40px;
      z-index: 99;
}
.aplayer-controller .aplayer-volume-wrap .aplayer-volume-bar-wrap .aplayer-volume-bar {
        position: absolute;
        bottom: 0;
        right: 10px;
        width: 5px;
        height: 35px;
        background: #aaa;
}
.aplayer-controller .aplayer-volume-wrap .aplayer-volume-bar-wrap .aplayer-volume-bar .aplayer-volume {
          position: absolute;
          bottom: 0;
          right: 0;
          width: 5px;
          -webkit-transition: all 0.1s ease;
          transition: all 0.1s ease;
}

.aplayer-lrc {
  position: relative;
  height: 30px;
  text-align: center;
  overflow: hidden;
  margin: -10px 0 7px;
}
.aplayer-lrc:before {
    position: absolute;
    top: 0;
    z-index: 1;
    display: block;
    overflow: hidden;
    width: 100%;
    height: 10%;
    content: ' ';
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(rgba(255, 255, 255, 0)));
    background: linear-gradient(to bottom, white 0%, rgba(255, 255, 255, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
}
.aplayer-lrc:after {
    position: absolute;
    bottom: 0;
    z-index: 1;
    display: block;
    overflow: hidden;
    width: 100%;
    height: 33%;
    content: ' ';
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.8)));
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ccffffff',GradientType=0 );
}
.aplayer-lrc p {
    font-size: 12px;
    color: #666;
    line-height: 16px !important;
    height: 16px !important;
    padding: 0 !important;
    margin: 0 !important;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
    opacity: 0.4;
    overflow: hidden;
}
.aplayer-lrc p.aplayer-lrc-current {
      opacity: 1;
      overflow: visible;
      height: initial !important;
}
.aplayer-lrc .aplayer-lrc-contents {
    width: 100%;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
    -webkit-user-select: text;
       -moz-user-select: text;
        -ms-user-select: text;
            user-select: text;
    cursor: default;
}
